
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
const getParams = function (url) {
let params = {}
if (url.includes('?')) {
let list = url.split('?')[1].split('&')
list.forEach(item => {
params[item.split('=')[0]] = decodeURIComponent(item.split('=')[1])
})
}
return params
}
export default {
components: {
Group,
Cell,
Toast
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
password:'',
params: {
'account': '',
'password':''
},
showPositionValue: false,
middle: 'middle',
msg:'msg',
params02:{
'path':'/tealogin'
},
params03:{
'sessionId':''
},
coachMsg:{

}
}
},
created () {
this.params03.sessionId = getCookie('sessionId')
var option = getParams(location.href)
if(option.status == '1' || option.status == '2'){
this.showPositionValue = true
this.msg='请使用账号密码登录后，绑定该微信后进行微信登录'
}
console.log(option)
if(this.params03.sessionId){
this.getCoachMsg()
}
this.judgeUserType()
},
methods: {
coachLogin () {
const that = this;
Drive.prototype.coachLogin(that.params).then(res => {
let list = res.data.data || {};

if(res.data.status == 2000000){
console.log(list.sessionId)

that.$router.push('../teacher')
}else if(res.data.status == 5000002){
that.showPositionValue = true
that.msg = '请重新登录'
} else{
that.showPositionValue = true
that.msg = res.data.message
}
});
},
judgeUserType () {
const that = this;
Drive.prototype.judgeUserType(that.params03).then(res => {
let list = res.data.data || {};

if(res.data.status == 2000000){
console.log(list.sessionId)

}else {
that.showPositionValue = true
that.msg = res.data.message
}
});
},
getCoachMsg(){
const that = this;
Drive.prototype.getCoachData(that.params03).then(res => {
let list = res.data.data || [];
that.coachMsg = list
console.log(that.coachMsg)
});
},
setCookie: function (cname, cvalue, exdays) {
var d = new Date()
d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
var expires = 'expires=' + d.toUTCString()
console.info(cname + "=" + cvalue + "; " + expires)
document.cookie = cname + "=" + cvalue + "; " + expires
console.info(document.cookie);
},
onLoginTap (){
this.coachLogin()
},
register(){







var url = 'http://jindou360.com:8080/jkrj/api/wechat/auth/register.jhtml?path=' + '/tealogin'
window.location.href = url
},
onRegisterTap(){
this.register()
}
}
}



